@import url("https://fonts.googleapis.com/css2?family=Lexend:wght@100;200;300;400;500;600;700;800;900&display=swap");

.navbar-container {
  position: fixed;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  height: 90px;
  background: rgba(19, 19, 19, 0.9);
  z-index: 20;
  backdrop-filter: blur(40px) saturate(125%);
  -webkit-backdrop-filter: blur(30px) saturate(125%);
  border-bottom: 2px solid #131313;
}

.send-msg {
  font-family: "Outfit", sans-serif;
  background: none;
  color: #21a362;
  cursor: pointer;
  border: 2px solid #21a362;
  font-weight: 400;
  font-size: 18px;
  letter-spacing: 0.024rem;
  width: auto;
  height: 4vh;
  border-radius: 8px;
  padding: 0 14px 0 14px;
}

.send-msg:hover {
  background: #21a362;
  color: #fff;
}

.link-buttons {
  font-family: "Outfit", sans-serif;
  font-weight: 400;
  font-size: 20px;
  line-height: 20px;
  letter-spacing: 0.025em;
  width: 100px;
  height: 48px;
  /* Inside Auto Layout */
  margin: 0px 20px;

  /* style */
  background: none;
  color: #fff;
  border: 0;
  outline: none;
  cursor: pointer;
  border-radius: 8px;
}

.navi-cons {
  display: inline;
}

.hamburger_container {
  display: none;
}

.hamburger .line {
  width: 35px;
  height: 3px;
  background-color: #ecf0f1;
  display: block;
  margin: 8px auto;
  -webkit-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

#hamburger-12.is-active .line:nth-child(1) {
  opacity: 0;
  -webkit-transform: translateX(-100%);
  -ms-transform: translateX(-100%);
  -o-transform: translateX(-100%);
  transform: translateX(-100%);
}

#hamburger-12.is-active .line:nth-child(3) {
  opacity: 0;
  -webkit-transform: translateX(100%);
  -ms-transform: translateX(100%);
  -o-transform: translateX(100%);
  transform: translateX(100%);
}

.three {
  width: 23.8%;
}

.col {
  display: block;
  float: left;
  margin: 1% 0 1% 1.6%;
}

.col:first-of-type {
  margin-left: 0;
}

.link-buttons:hover {
  background: #202020;
  color: #21a362;
}

.activated {
  color: #21a362;
  text-decoration: none;
  display: inline-block;
  position: relative;
}

.active {
  text-decoration: none;
  display: inline-block;
  position: relative;
}

@-webkit-keyframes indicator {
  30% {
    opacity: 0;
  }
  0% {
    opacity: 1;
  }
  100% {
    opacity: 1;
    border: 3px solid #60d480;
    left: -68%;
  }
}

@keyframes indicator {
  30% {
    opacity: 0;
  }
  0% {
    opacity: 1;
  }
  100% {
    opacity: 1;
    border: 3px solid #60d480;
    left: -68%;
  }
}

body {
  margin: 0;
}

@media screen and (max-width: 960px) {
  .navi-cons {
    display: none;
  }

  .hamburger_container {
    margin-right: 5%;
    display: block;
  }
}
