@font-face {
  font-family: lab-bold;
  src: url(../Assets/Lab\ Grotesque/LabGrotesque-Bold.ttf);
}

@font-face {
  font-family: lab-light;
  src: url(../Assets/Lab\ Grotesque/LabGrotesque-Light.ttf);
}

body {
  user-select: none;
}

.showreel {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
  filter: brightness(0.6);
}

.hero-section {
  position: relative;
  padding-bottom: 10px;
  height: 1080px;
  z-index: 5;
}

.hero-container {
  height: 80vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  object-fit: none;
  justify-content: center;
  color: white;
  position: relative;
  z-index: 5;
}

.hero-container > h1 {
  font-family: "lab-bold", sans-serif;
  font-weight: 600;
  letter-spacing: -0.25rem;
  font-size: 100px;
}

.hero_subtitle > p {
  font-family: "lab-light", sans-serif;
  color: white;
  font-size: 28px;
  font-weight: 200;
  margin-right: 20px;
  letter-spacing: -0.05rem;
}

.hero_subtitle {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.hero-btns {
  margin-top: 24px;
}

.hero-btns .btn {
  margin: 6px;
}

.arrow_right {
  position: relative;
  left: -10px;
  width: 0;
  opacity: 0;
  transition: 150ms ease-in-out;
}

.drop-msg {
  font-family: "Outfit", sans-serif;
  background: none;
  color: white;
  box-sizing: border-box;

  /* Auto layout */

  display: flex;
  flex-direction: row;
  align-items: flex-start;
  font-size: 22px;
  padding: 10px 30px;
  gap: 10px;

  /* Primary/Base */

  border: 2px solid #21a362;

  /* Inside auto layout */

  flex: none;
  order: 2;
  flex-grow: 0;
  cursor: pointer;
  transition: all 150ms ease-in-out;
}

.drop-msg:hover {
  color: white;
  background: #21a362;
}

.btn {
  transition: all 150ms ease-in-out;
}

.btn:hover .arrow_right {
  left: 5px;
  width: 6.6px;
  opacity: 100%;
}

@media screen and (max-width: 1920px) {
  .hero-section > h1 {
    font-size: 60px;
    margin-bottom: 35vh;
  }

  .hero_subtitle > p {
    font-size: 18px;
  }
}

@media screen and (max-width: 960px) {
  .hero-container > h1 {
    font-size: 60px;
  }

  .hero_subtitle > p {
    font-size: 18px;
  }
}

@media screen and (max-width: 760px) {
  .hero-container > h1 {
    font-size: 40px;
  }

  .hero_subtitle > p {
    font-size: 16px;
  }

  .hero-btns {
    font-size: 20px;
  }

  .btn {
    width: 100%;
  }
}
